<template>
  <div>
    <v-card id="workExperiences" class="common-card mb-5">
      <v-card-title class="mb-7 pa-0"
        ><span class="text-gradient mainFont--text">{{ terms.experiences }}</span></v-card-title
      >
      <v-card-text class="pa-0">
        <span class="work-exp">
          <ul class="pa-0">
            <li
              style="list-style: none"
              :class="index != experiences.length - 1 ? 'pb-10' : ''"
              v-for="(item, index) in experiences"
              :key="index"
            >
              <div class="d-flex align-start">
                <img
                  :src="item.logo"
                  width="60"
                  height="60"
                  class="rounded mt-1 mb-4"
                  :class="isLtr ? 'mr-4' : 'me-4'"
                />

                <div>
                  <!-- Company name -->
                  <h3 class="greyColor--text">{{ item[lang + 'name'] }}</h3>
                  <!-- Duration of experience -->
                  <p
                    class="mb-0 mt-1 grey--text text--darken-1"
                    :class="!isLtr ? 'persian-font' : ''"
                  >
                    {{ item[lang + 'duration'] }}
                  </p>
                  <!-- Location of experience -->
                  <p class="grey--text mb-2 text--darken-1">
                    {{ item[lang + 'location'] }}
                  </p>
                </div>
              </div>
              <!-- Position of experience -->
              <p
                class="position-box mt-0 greyColor--text mb-0"
                :style="
                  isLtr
                    ? 'padding-left: 10px; border-left: 3px solid ' +
                      (!$vuetify.theme.dark ? '#406882' : '#9D5353')
                    : 'padding-right: 10px; border-right: 3px solid ' +
                      (!$vuetify.theme.dark ? '#406882' : '#9D5353')
                "
                :class="!$vuetify.theme.dark ? 'text--darken-1' : ''"
              >
                {{ item[lang + 'position'] }}
              </p>
            </li>
          </ul>
        </span>
      </v-card-text>
    </v-card>
  </div>
</template>

<script>
import utilities from './../mixin/utilities';
export default {
  mixins: [utilities],
};
</script>
