<template>
  <div>
    <v-card id="profile" class="common-card profile pa-0">
      <!-- The header of profile -->
      <div class="header bgGrey text-center w-100 py-6">
        <!-- Profile Image box -->
        <div class="image-box mx-auto">
          <img src="/parnia.jpg" />
        </div>
        <!-- Profile name -->
        <p class="name mt-4 mb-0" :class="!$vuetify.theme.dark ? 'font-weight-bold' : ''">
          {{ terms.name }}
        </p>
        <!-- Profile position -->
        <p class="position greyColor--text mb-0">{{ terms.jobTitle }}</p>
      </div>

      <!-- Residence and Age Info section -->
      <div class="pa-6">
        <v-row>
          <v-col>
            <span class="fs-small" :class="!$vuetify.theme.dark ? 'font-weight-bold' : ''"
              >{{ terms.residence }}:
            </span></v-col
          >
          <v-col class="greyColor--text" :class="isLtr ? 'text-right' : 'text-left'">
            <span class="fs-small">{{ terms.residenceCountry }}, {{ terms.residenceCity }}</span>
          </v-col>
        </v-row>
        <v-row>
          <v-col class="pt-0">
            <span class="fs-small" :class="!$vuetify.theme.dark ? 'font-weight-bold' : ''"
              >{{ terms.age }}:
            </span></v-col
          >
          <v-col class="greyColor--text pt-0" :class="isLtr ? 'text-right' : 'text-left'">
            <span class="fs-small" :class="!isLtr ? 'persian-font' : ''">{{
              Number(currentYear) - 1997
            }}</span>
          </v-col>
        </v-row>
      </div>

      <v-divider class="mx-6"></v-divider>

      <!-- Languages section -->
      <div class="pa-4">
        <v-row>
          <v-col v-for="(item, index) in languages" :key="index" class="text-center px-0">
            <!-- language progress -->
            <v-progress-circular
              :rotate="360"
              :size="62"
              :width="5"
              :value="item.progress"
              color="secondary"
            >
              <!-- language progress value -->
              <span class="font-weight-bold" :class="!isLtr ? 'persian-font' : ''"
                >{{ item.progress }}%</span
              >
            </v-progress-circular>
            <!-- Language title -->
            <p class="mt-2 mb-0 greyColor--text text--darken-1" style="font-size: 0.81rem">
              {{ item[lang + 'name'] }}
            </p>
            <p
              style="font-size: 0.72rem"
              class="mb-0"
              :class="!$vuetify.theme.dark ? 'font-weight-bold' : ''"
              v-if="item.ENname == 'Persian'"
            >
              ({{ terms.native }})
            </p>
          </v-col>
        </v-row>
      </div>

      <v-divider class="mx-6"></v-divider>

      <!-- Skills section -->
      <div id="skills" class="pa-6">
        <h2 class="mb-6" style="font-weight: 400 !important">{{ terms.skills }}</h2>
        <div>
          <div v-for="(item, index) in skills" :key="index" class="mb-4">
            <div class="d-flex">
              <v-icon color="secondary">mdi-circle-medium</v-icon>
              <span class="fs-small greyColor--text">{{ item.name }}</span>
              <span v-if="item.familiar" class="px-3">
                <v-chip outlined color="secondary" style="height: 20px">
                  <span class="fs-xxsmall">{{ terms.familiar }}</span>
                </v-chip>
              </span>
            </div>
          </div>
        </div>
      </div>
    </v-card>
  </div>
</template>

<script>
import utilities from './../mixin/utilities';
export default {
  mixins: [utilities],
  data() {
    return {
      updating: false,
    };
  },
};
</script>
