<template>
  <div>
    <vue-html2pdf
      :show-layout="false"
      :float-layout="true"
      :enable-download="true"
      :preview-modal="false"
      :filename="`ParniaMaroofi-${lang}`"
      :pdf-quality="2"
      :manual-pagination="true"
      pdf-format="a3"
      pdf-orientation="portrait"
      pdf-content-width="100%"
      :paginate-elements-by-height="1000"
      @hasDownloaded="hasDownloaded($event)"
      ref="html2Pdf"
    >
      <section slot="pdf-content">
        <div class="pdf-header" style="height: 200px; background-color: #455a64; postion: relative">
          <div
            style="position: absolute; top: 27px"
            :style="isLtr ? 'right: 70px' : 'right: 450px; text-align: right'"
            :dir="isLtr ? 'ltr' : 'rtl'"
          >
            <p
              style="color: #fff"
              :style="
                isLtr
                  ? 'font-size: 70px; margin-bottom: 0; transform: translateX(-3px)'
                  : 'font-size: 45px; margin-bottom: 10px'
              "
            >
              {{ terms.name }}
            </p>
            <p
              class="mb-1"
              style="font-size: 27px; color: #fff; transform: translateY(-18px)"
              :style="isLtr ? 'font-size: 27px' : 'font-size: 22px;'"
            >
              {{ terms.jobTitle }}
            </p>
            <div style="font-size: 13px; color: #fff; transform: translateY(-4px)">
              * {{ terms.accessToWebsite }}
            </div>
          </div>
        </div>
        <div style="display: flex" :dir="isLtr ? 'ltr' : 'rtl'">
          <div
            style="
              background-color: #dedede;
              width: 700px;
              padding-top: 160px;
              padding-bottom: 70px;
              position: relative;
              text-align: center;
            "
          >
            <div style="position: absolute; top: -130px; left: 50%; transform: translateX(-50%)">
              <img style="width: 260px; height: 260px; border-radius: 50%" src="/parnia.jpg" />
            </div>
            <!-- -------------------------- Contact -------------------------------- -->
            <p
              style="font-size: 25px; margin-bottom: 20px"
              :style="!isLtr ? 'margin-top: -10px' : ''"
            >
              {{ terms.contact }}
            </p>

            <p style="font-size: 1.2rem; margin-bottom: 2px">{{ terms.phone }}</p>
            <p
              style="font-size: 1.1rem; margin-bottom: 15px; color: #4a4a4a"
              dir="ltr"
              :class="!isLtr ? 'persian-font' : ''"
            >
              (+98) 9226907483
            </p>

            <p style="font-size: 1.2rem; margin-bottom: 2px">{{ terms.email }}</p>
            <p style="font-size: 1.1rem; margin-bottom: 17px; color: #4a4a4a">
              parniamaroofi@gmail.com
            </p>

            <p style="font-size: 1.2rem; margin-bottom: 2px">Github</p>
            <p style="font-size: 1.1rem; margin-bottom: 17px; color: #4a4a4a">
              github.com/parniamaroofi
            </p>

            <p style="font-size: 1.2rem; margin-bottom: 2px">Linkedin</p>
            <p style="font-size: 1.1rem; color: #4a4a4a">www.linkedin.com/in/parniamaroofi</p>

            <p
              style="
                font-size: 25px;
                margin-top: 50px;
                margin-bottom: 20px;
                text-transform: uppercase;
              "
              :style="isLtr ? 'margin-top: 70px;' : 'margin-top: 60px;'"
            >
              {{ terms.skills }}
            </p>
            <!-- -------------------------- Skills -------------------------------- -->
            <div style="padding: 0 30px">
              <v-row>
                <v-col
                  v-for="(item, index) in skills"
                  :key="`skills${index}`"
                  cols="6"
                  style="padding: 10px !important"
                >
                  <div style="display: flex; justify-content: space-between">
                    <span style="font-size: 1rem">{{ item.name }}</span>
                  </div>
                  <div style="margin-top: 5px">
                    <v-progress-linear
                      color="#455A64"
                      :value="item.progress"
                      rounded
                      height="7"
                    ></v-progress-linear>
                  </div>
                </v-col>
              </v-row>
            </div>

            <p
              style="
                font-size: 25px;
                margin-top: 80px;
                margin-bottom: 15px;
                text-transform: uppercase;
              "
              :style="isLtr ? 'margin-top: 90px;' : 'margin-top: 77px;'"
            >
              {{ terms.softSkills }}
            </p>
            <!-- -------------------------- Soft Skills -------------------------------- -->
            <div>
              <p
                v-for="(item, index) in softSkills"
                :key="`softSkills${index}`"
                :style="
                  index != softSkills.length - 1 ? 'margin-bottom: 17px' : 'margin-bottom: 0px'
                "
                class="fs-xxlarge"
              >
                <v-icon :style="isLtr ? 'margin-right: 5px' : 'margin-left: 5px'" color="#455A64"
                  >mdi-checkbox-marked-outline</v-icon
                >{{ item[lang + 'name'] }}
              </p>
            </div>
          </div>
          <div style="background-color: #fff; width: 100%; padding: 40px">
            <!-- -------------------------- About Me -------------------------------- -->
            <p style="font-size: 25px; margin-bottom: 15px; text-transform: uppercase">
              {{ terms.aboutMe }}
            </p>
            <p style="font-size: 0.98rem; text-align: justify">{{ aboutMe[lang] }}</p>
            <!-- -------------------------- Activities -------------------------------- -->
            <p
              style="
                font-size: 25px;
                margin-top: 50px;
                margin-bottom: 15px;
                text-transform: uppercase;
              "
            >
              {{ terms.activities }}
            </p>
            <ul>
              <li
                style="font-size: 1rem; list-style: none"
                v-for="(item, index) in activities"
                :key="`activities${index}`"
              >
                <v-icon :style="isLtr ? 'margin-right: 5px' : 'margin-left: 5px'" color="#455A64"
                  >mdi-checkbox-marked-outline</v-icon
                >
                {{ item[lang] }}
              </li>
            </ul>
            <!-- -------------------------- Educations -------------------------------- -->
            <p
              style="
                font-size: 25px;
                margin-top: 50px;
                margin-bottom: 15px;
                text-transform: uppercase;
              "
            >
              {{ terms.educations }}
            </p>
            <div v-for="(item, index) in educations" :key="`educations${index}`">
              <div style="display: flex; justify-content: space-between">
                <p style="font-size: 1.2rem; margin-bottom: 3px">
                  {{ item[lang + 'name'] }}
                </p>
                <p
                  style="font-size: 0.9rem; width: 350px; margin-top: 5px"
                  :style="isLtr ? 'text-align: right' : 'text-align: left'"
                  :class="!isLtr ? 'persian-font' : ''"
                >
                  {{ item[lang + 'duration'] }}
                </p>
              </div>
              <p style="font-size: 1rem; color: #777; margin-bottom: 3px">
                {{ item[lang + 'degree'] }}
              </p>
              <p style="font-size: 1rem; color: #777">{{ item[lang + 'location'] }}</p>
            </div>
            <!-- -------------------------- Experiences -------------------------------- -->
            <p
              style="
                font-size: 25px;
                margin-top: 50px;
                margin-bottom: 15px;
                text-transform: uppercase;
              "
            >
              {{ terms.experiences }}
            </p>
            <div v-for="(item, index) in experiences" :key="`experiences${index}`">
              <div style="display: flex; justify-content: space-between">
                <p style="font-size: 1.2rem; margin-bottom: 3px">
                  {{ item[lang + 'name'] }}
                </p>
                <p
                  style="font-size: 0.9rem; width: 350px; margin-top: 5px"
                  :style="isLtr ? 'text-align: right' : 'text-align: left'"
                  :class="!isLtr ? 'persian-font' : ''"
                >
                  {{ item[lang + 'duration'] }}
                </p>
              </div>
              <p style="font-size: 1rem; color: #777">{{ item[lang + 'location'] }}</p>

              <p
                style="font-size: 1rem; color: #777; margin-top: 10px"
                :style="
                  isLtr
                    ? 'padding-left: 10px; border-left: 3px solid #406882'
                    : 'padding-right: 10px; border-right: 3px solid #406882'
                "
              >
                {{ item[lang + 'position'] }}
              </p>
            </div>
            <!-- -------------------------- Languages -------------------------------- -->
            <p
              style="
                font-size: 25px;
                margin-top: 52px;
                margin-bottom: 30px;
                text-transform: uppercase;
              "
            >
              {{ terms.languages }}
            </p>
            <v-row>
              <v-col
                v-for="(item, index) in languages"
                :key="`languages${index}`"
                style="text-align: center"
              >
                <!-- language progress -->
                <v-progress-circular
                  :rotate="360"
                  :size="90"
                  :width="8"
                  :value="item.progress"
                  color="#455A64"
                >
                  <!-- language progress value -->
                  <span style="font-size: 1rem" :class="!isLtr ? 'persian-font' : ''"
                    >{{ item.progress }}%</span
                  >
                </v-progress-circular>
                <!-- Language title -->
                <p style="font-size: 1rem; margin-top: 10px; color: #4a4a4a">
                  {{ item[lang + 'name'] }}
                </p>
                <p style="font-size: 0.8rem; margin-bottom: 0" v-if="item.ENname == 'Persian'">
                  ({{ terms.native }})
                </p>
              </v-col>
            </v-row>
          </div>
        </div>
      </section>
    </vue-html2pdf>
  </div>
</template>

<script>
import VueHtml2pdf from 'vue-html2pdf';
import { EventBus } from './../event-bus';
import utilities from './../mixin/utilities';
export default {
  components: {
    VueHtml2pdf,
  },
  mixins: [utilities],
  data() {
    return {};
  },
  methods: {
    generateReport() {
      EventBus.$emit('download-event', true);

      setTimeout(() => {
        this.$refs.html2Pdf.generatePdf();
      }, 200);
    },
    hasDownloaded() {
      EventBus.$emit('download-event', false);
    },
  },
  mounted() {
    const thisInstance = this;
    this.$root.$on('generateReportEvent', function () {
      thisInstance.generateReport();
    });
  },
};
</script>
