<template>
  <div>
    <v-app-bar color="primary" dark>
      <!-- Buttons of navigation items -->
      <div class="d-none d-md-flex flex-row">
        <div v-for="(item, index) in navItems" :key="index">
          <span class="hover">
            <v-btn @click="$emit('goTo', item.href)" text height="53" class="nav-btn px-3">{{
              item[lang + 'Title']
            }}</v-btn></span
          >

          <!-- The square icon to separate items -->
          <v-icon v-if="index != navItems.length - 1" x-large color="#90A4AE"
            >mdi-square-small</v-icon
          >
        </div>
      </div>

      <!-- Menu icon - It will be shown on the MOBILE device -->
      <div @click.stop="drawer = !drawer" class="d-md-none d-block pointer">
        <v-icon>mdi-menu</v-icon>
      </div>

      <v-spacer></v-spacer>

      <!-- Languages Menu -->
      <div :class="isLtr ? 'mr-6' : 'me-6'">
        <v-menu transition="slide-y-transition" offset-y bottom>
          <template v-slot:activator="{ on, attrs }">
            <div v-bind="attrs" v-on="on" class="d-flex flex-row align-center">
              <span class="pt-1" :class="isLtr ? 'pr-1' : 'pe-2'">{{ language.value }}</span>
              <span><country-flag :country="language.flagCode ? language.flagCode : ''" /></span>
            </div>
          </template>
          <v-list>
            <v-list-item v-for="(item, i) in langs" :key="i" @click="setLanguage(item)">
              <v-list-item-title>
                <div class="d-flex flex-row align-center justify-end">
                  <span class="pt-1" :class="isLtr ? 'pr-1' : 'pe-2'"
                    ><span class="grey--text fs-xsmall">({{ item.text }})</span> {{ item.value }}
                  </span>
                  <span><country-flag :country="item.flagCode" /></span>
                </div>
              </v-list-item-title>
            </v-list-item>
          </v-list>
        </v-menu>
      </div>

      <!-- The switch button for light/dark mode -->
      <div @click="changeThemeMode" class="pointer white--text d-flex flex-row ps-1">
        <v-icon class="nav-icon" v-if="$vuetify.theme.dark">$sun</v-icon>
        <v-icon class="nav-icon" v-else>$moon</v-icon>
      </div>
    </v-app-bar>

    <!-- Sidebar navigation - It will be shown on the mobile device -->
    <v-navigation-drawer v-model="drawer" fixed :right="!isLtr" :left="isLtr">
      <v-list nav dense>
        <div
          @click="
            $emit('goTo', 'profile');
            drawer = false;
          "
          class="header bgGrey d-flex flex-row w-100 py-4"
        >
          <div class="image-box mx-3">
            <img src="/parnia.jpg" />
          </div>
          <div>
            <p class="name mt-3 mb-2 mainFont--text">{{ terms.name }}</p>
            <p class="position greyColor--text mb-0">{{ terms.jobTitle }}</p>
          </div>
        </div>
        <v-list-item-group v-for="(item, index) in navItems" :key="index" class="mx-1">
          <v-list-item
            @click="
              $emit('goTo', item.href);
              drawer = false;
            "
          >
            <v-list-item-title
              ><v-icon class="pb-1">{{ item.icon }}</v-icon
              ><span class="mx-6">{{ item[lang + 'Title'] }}</span></v-list-item-title
            >
          </v-list-item>
        </v-list-item-group>
      </v-list>
    </v-navigation-drawer>
  </div>
</template>

<script>
export default {
  data() {
    return {
      drawer: false,
      navItems: [
        {
          ENTitle: 'About me',
          FATitle: 'درباره من',
          KOTitle: '나에 대하여',
          icon: 'mdi-account-outline',
          href: 'about',
        },
        {
          ENTitle: 'Activities',
          FATitle: 'فعالیت‌ها',
          KOTitle: '활동',
          icon: 'mdi-text-box-outline',
          href: 'activities',
        },
        {
          ENTitle: 'Educations',
          FATitle: 'سوابق تحصیلی',
          KOTitle: '학력',
          icon: 'mdi-school-outline',
          href: 'educations',
        },
        {
          ENTitle: 'Work Experiences',
          FATitle: 'سوابق شغلی',
          KOTitle: '업무 경험',
          icon: 'mdi-briefcase-outline',
          href: 'workExperiences',
        },
        {
          ENTitle: 'Skills',
          FATitle: 'مهارت‌ها',
          KOTitle: '스킬',
          icon: 'mdi-laptop',
          href: 'skills',
        },
      ],
    };
  },
  methods: {
    changeThemeMode() {
      this.$vuetify.theme.dark = !this.$vuetify.theme.dark;
      localStorage.setItem('dark', this.$vuetify.theme.dark);
    },
    setLanguage(item) {
      setTimeout(() => {
        this.setLang(item.value);
        localStorage.setItem('language', JSON.stringify(item));
        this.language = item;
        if (this.language.value == 'FA') {
          this.$vuetify.rtl = true;
          this.updateDirection(false);
        } else {
          this.$vuetify.rtl = false;
          this.updateDirection(true);
        }
      }, 50);
    },
  },
  mounted() {},
};
</script>
