<template>
  <div>
    <v-card id="educations" class="common-card mb-5">
      <v-card-title class="mb-5 pa-0">
        <span class="text-gradient mainFont--text">{{ terms.educations }}</span></v-card-title
      >
      <v-card-text class="pa-0">
        <span class="education">
          <ul class="pa-0">
            <li v-for="(item, index) in educations" :key="index" style="list-style: none">
              <!-- University name -->
              <h3 class="greyColor--text">
                {{ item[lang + 'name'] }}
              </h3>
              <!-- Education duration -->
              <p class="mb-0 mt-1 grey--text text--darken-1" :class="!isLtr ? 'persian-font' : ''">
                {{ item[lang + 'duration'] }}
              </p>
              <!-- Education degree -->
              <p class="mb-0 grey--text text--darken-1">{{ item[lang + 'degree'] }}</p>
              <!-- Location of education -->
              <p class="grey--text text--darken-1 mb-0">{{ item[lang + 'location'] }}</p>
            </li>
          </ul>
        </span>
      </v-card-text>
    </v-card>
  </div>
</template>

<script>
import utilities from './../mixin/utilities';
export default {
  mixins: [utilities],
};
</script>
