<template>
  <div>
    <div class="page-header" :style="background()">
      <!-- The button to download resume PDF -->
      <!-- @click="$root.$emit('generateReportEvent')" -->
      <div class="download-btn" :class="isLtr ? 'right' : 'left'" @click="doDownload()">
        <v-btn :loading="downloading">
          <template v-slot:loader>
            <span class="pdf-btn d-flex white--text">
              {{ terms.downloading }}
              <div class="snippet ms-3 mt-2" data-title=".dot-elastic">
                <div class="stage">
                  <div class="dot-elastic"></div>
                </div>
              </div>
            </span>
          </template>
          <span class="pdf-btn">{{ terms.downloadResume }}</span></v-btn
        >
      </div>

      <div
        :class="isLtr ? 'left-text-box1' : 'right-text-box1'"
        class="animate__animated animate__fadeInDown"
        v-if="show"
      >
        {{ terms.name }} - {{ terms.programmer }}
      </div>

      <div
        :class="
          isLtr
            ? 'left-text-box2 animate__animated animate__fadeInLeft'
            : 'right-text-box2 animate__animated animate__fadeInRight'
        "
        v-if="show"
      >
        {{ terms.jobTitle }}
      </div>

      <div
        :class="isLtr ? 'left-text-box3' : 'right-text-box3'"
        class="animate__animated animate__fadeInUp"
        v-if="show"
      >
        {{ terms.resumeWebsite }}
      </div>

      <!-- Social media icons -->
      <div :class="isLtr ? 'right-icons-box' : 'left-icons-box'">
        <!-- Github Icon -->
        <a target="blank" href="https://github.com/parniamaroofi">
          <div class="pointer">
            <v-icon>mdi-github</v-icon>
          </div>
        </a>
        <!-- Linkedin Icon -->
        <a target="blank" href="https://www.linkedin.com/in/parniamaroofi">
          <div class="pointer">
            <v-icon>mdi-linkedin</v-icon>
          </div>
        </a>
        <!-- Gmail Icon -->
        <div @click="ShowGmailAddress()" class="pointer d-none d-md-block">
          <v-icon>mdi-gmail</v-icon>
        </div>
        <div class="d-block d-md-none">
          <a href="mailto:parniamaroofi@gmail.com"> <v-icon>mdi-gmail</v-icon></a>
        </div>
      </div>

      <!-- Gmail address snackbar -->
      <v-snackbar v-model="gmailSnackbar" vertical dir="ltr" :timeout="-1" color="primary">
        <v-icon :class="isLtr ? 'pr-3' : 'ps-3'">$gmail</v-icon>
        <span class="fs-xsmall pt-1"> {{ gmailAddress }}</span>

        <template v-slot:action="{ attrs }">
          <v-btn
            v-clipboard:copy="gmailAddress"
            v-clipboard:success="onCopy"
            :disabled="copied"
            color="info lighten-2"
            text
          >
            <span v-if="!copied">{{ terms.copy }}</span>
            <span v-else class="green--text" :class="isLtr ? 'pr-2' : 'ps-2'">{{
              terms.copiedToClipboard
            }}</span>
          </v-btn>
          <v-btn
            :class="isLtr ? 'mr-2' : 'ms-2'"
            color="red lighten-2"
            text
            v-bind="attrs"
            @click="gmailSnackbar = false"
            v-if="!copied"
          >
            <span>{{ terms.close }}</span>
          </v-btn>
        </template>
      </v-snackbar>

      <!-- Mouse wheel sign -->
      <div @click="$emit('goTo', 'profile')" id="mouse-btn" class="pointer">
        <span class="mouse d-sm-flex d-none">
          <span class="mouse-wheel"> </span>
        </span>
      </div>
    </div>
  </div>
</template>

<script>
import { EventBus } from './../event-bus.js';
export default {
  data() {
    return {
      show: true,
      copied: false,
      downloading: false,
      gmailSnackbar: false,
      gmailAddress: 'parniamaroofi@gmail.com',
    };
  },
  methods: {
    background() {
      if (!this.$vuetify.theme.dark) {
        return 'background-image: url("/5.jpg")';
      } else {
        return 'background-image: url("/1.jpg")';
      }
    },
    onCopy() {
      this.copied = true;
      setTimeout(() => {
        this.gmailSnackbar = false;
      }, 2000);
    },
    ShowGmailAddress() {
      this.copied = false;
      this.gmailSnackbar = true;
    },
    doDownload() {
      this.downloading = true;
      setTimeout(() => {
        this.downloading = false;
        let btn = document.createElement('a');
        btn.setAttribute('href', '/ParniaMaroofi.CV.pdf');
        btn.setAttribute('download', 'ParniaMaroofi.CV.pdf');
        btn.click();
      }, 1000);
    },
  },
  watch: {
    isLtr() {
      this.show = false;
      setTimeout(() => {
        this.show = true;
      }, 1);
    },
  },
  mounted() {
    EventBus.$on('download-event', (downloading) => {
      this.downloading = downloading;
    });
  },
};
</script>
